import { runWaterfall } from '../src';
import './styles.css';
document.addEventListener('DOMContentLoaded', function () {
    var textArea = document.querySelector('#adTag');
    var testBtn = document.querySelector('.vast-test-btn');
    var videoElement = document.querySelector('.vast-media video');
    var videoAdContainer = document.querySelector('.video-ad-container');
    var adUnit;
    var onAdReady = function (newAdUnit) {
        adUnit = newAdUnit;
        console.log('### onAdReady', adUnit);
        var eventHandler = function (event) {
            console.log("### ".concat(event.type), event.adUnit);
        };
        [
            'pause',
            'resume',
            'finish',
            'impression',
            'start',
            'skip',
            'firstQuartile',
            'midpoint',
            'thirdQuartile',
            'complete'
        ].forEach(function (eventType) {
            adUnit.on(eventType, eventHandler);
        });
    };
    var requestAdRun = function () {
        var adTag = textArea.value;
        var src = videoElement.src;
        var resumeContent = function () {
            videoElement.play();
            videoElement.removeEventListener('contentloadedmetadata', resumeContent);
            videoElement.removeEventListener('canplay', resumeContent);
        };
        var onError = function (error) {
            console.log('### onError', error);
        };
        var onRunFinish = function () {
            console.log('### onRunFinish');
            videoAdContainer.classList.remove('active');
            if (videoElement.src === src) {
                videoElement.play();
            }
            else {
                videoElement.addEventListener('contentloadedmetadata', resumeContent);
                videoElement.addEventListener('canplay', resumeContent);
                videoElement.src = src;
                videoElement.load();
            }
        };
        videoAdContainer.classList.add('active');
        videoElement.pause();
        videoElement.currentTime = 0;
        console.log('### adTag', adTag);
        runWaterfall(adTag, videoAdContainer, {
            onAdReady: onAdReady,
            onError: onError,
            onRunFinish: onRunFinish,
            timeout: 15000,
            videoElement: videoElement,
            credentials: 'omit'
        });
    };
    textArea.addEventListener('change', function () {
        var adTag = textArea.value;
        if (adTag) {
            testBtn.classList.add('active');
            testBtn.addEventListener('click', requestAdRun);
        }
        else {
            testBtn.classList.remove('active');
            testBtn.removeEventListener('click', requestAdRun);
        }
    });
});
